import {
  Info as AboutIcon,
  Assessment as StatisticsIcon,
  Shuffle as SwitchIcon,
} from '@mui/icons-material';

import {
  Divider,
  ListItemIcon,
  Menu,
  MenuItem,
  MenuProps,
} from '@mui/material';
import { PopupState, bindMenu } from 'material-ui-popup-state/core';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useDatasetInformation } from '../Loader';

export const GlobalMenu = React.forwardRef<
  React.ComponentRef<typeof Menu>,
  Omit<MenuProps, 'open'> & { state: PopupState }
>(function GlobalMenu({ state, ...props }, ref) {
  const navigate = useNavigate();
  const datasetInfo = useDatasetInformation();

  return (
    <Menu {...bindMenu(state)} {...props} ref={ref}>
      <MenuItem
        onClick={() => {
          state.close();
          navigate('settings');
        }}
      >
        <ListItemIcon>
          <SwitchIcon fontSize="small" />
        </ListItemIcon>
        Switch dataset
      </MenuItem>
      <MenuItem
        disabled={
          datasetInfo.loading || !!datasetInfo.error || !datasetInfo.loaded
        }
        onClick={() => {
          state.close();
          navigate('statistics');
        }}
      >
        <ListItemIcon>
          <StatisticsIcon fontSize="small" />
        </ListItemIcon>
        Dataset statistics
      </MenuItem>
      <Divider />
      <MenuItem
        onClick={() => {
          state.close();
          navigate('about');
        }}
      >
        <ListItemIcon>
          <AboutIcon fontSize="small" />
        </ListItemIcon>
        About
      </MenuItem>
    </Menu>
  );
});
