import {
  Add as AddIcon,
  Airlines as AircraftOperatorIcon,
  Delete as DeleteIcon,
  ExpandMore as ExpandMoreIcon,
  Visibility as VisibilityIcon,
  VisibilityOff as VisibilityOffIcon,
} from '@mui/icons-material';

import { ColorFilterIcon } from './ColorFilterIcon';

import {
  Autocomplete,
  Avatar,
  Box,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  CardHeader,
  CardProps,
  Collapse,
  Divider,
  Fab,
  IconButton,
  TextField,
  useMediaQuery,
  useTheme,
} from '@mui/material';

import { schemeTableau10 } from 'd3-scale-chromatic';
import React, { useState, useMemo } from 'react';
import { CirclePicker } from 'react-color';
import { TransitionGroup } from 'react-transition-group';
import type { ListActions } from 'react-use/lib/useList';
import { v4 } from 'uuid';
import type { ColorFilterState } from '.';
import { useRankedAircraftOperators } from '../Loader/useRankedAircraftOperators';
import { getFilterGradient, getRandomPaletteColor } from './utils';

type FilterType = ColorFilterState['type'];

export const ColorFilterEditionList: React.FC<{
  filters: Array<ColorFilterState>;
  actions: ListActions<ColorFilterState>;
}> = function ColorFilterEditionList({ filters, actions }) {
  return (
    <>
      <TransitionGroup>
        {filters.map((f, idx) => (
          <Collapse key={f.id}>
            <ColorFilterEditionCard
              sx={{ mb: 1 }}
              filter={f}
              toggable={filters.length > 1}
              deletable={idx !== filters.length - 1}
              idx={idx}
              actions={actions}
              // We need to determine somehow if the filter has just been added
              initiallyExpanded={f.displayName === ''}
            />
          </Collapse>
        ))}
      </TransitionGroup>

      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-end',
          alignItems: 'center',
        }}
      >
        <Fab
          onClick={() => {
            actions.insertAt(filters.length - 1, {
              id: v4(),
              type: 'aircraftOperator',
              displayName: '',
              aircraftOperators: [],
              enabled: true,
              color: getRandomPaletteColor(),
            });
          }}
          color="primary"
          variant="extended"
        >
          <AddIcon />
        </Fab>
      </Box>
    </>
  );
};

const ColorFilterEditionCard = React.forwardRef<
  React.ComponentRef<typeof Card>,
  {
    filter: ColorFilterState;
    deletable: boolean;
    toggable: boolean;
    idx: number;
    actions: ListActions<ColorFilterState>;
    initiallyExpanded?: boolean;
  } & CardProps
>(function ColorFilterEditionCard(
  { filter, idx, actions, initiallyExpanded, deletable, toggable, ...rest },
  ref,
) {
  const expandable = filter.type === 'aircraftOperator';

  const [expanded, setExpanded] = useState(expandable && !!initiallyExpanded);
  const theme = useTheme();
  const rankedAos = useRankedAircraftOperators();

  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const filterColorString = `rgb(${filter.color[0]}, ${filter.color[1]}, ${filter.color[2]})`;

  const subheader: Partial<Record<FilterType, string>> = {
    aircraftOperator: 'Aircraft operator',
    callsign: 'Callsign',
    catchAll: 'Anything',
    altitudeGradient: 'Altitude gradient',
    airport: 'Airport',
  };

  const colorGradient = useMemo(() => getFilterGradient(filter), [filter]);

  return (
    <Card ref={ref} {...rest}>
      <CardActionArea
        onClick={
          expandable
            ? () => {
                setExpanded((prev) => !prev);
              }
            : undefined
        }
      >
        <CardHeader
          title={filter.displayName}
          subheader={subheader[filter.type]}
          avatar={
            <Avatar
              sx={
                filter.enabled
                  ? {
                      background: colorGradient ?? filterColorString,
                      color: theme.palette.getContrastText(filterColorString),
                    }
                  : {
                      color: filterColorString,
                      background: 'transparent',
                      border: `1px solid ${filterColorString}`,
                    }
              }
            >
              {<ColorFilterIcon type={filter.type} />}
            </Avatar>
          }
          action={
            expandable ? (
              <IconButton component="div" disableRipple>
                <ExpandMoreIcon
                  sx={{
                    transform: !expanded ? 'rotate(0deg)' : 'rotate(180deg)',
                    transition: theme.transitions.create('transform', {
                      duration: theme.transitions.duration.shortest,
                    }),
                  }}
                />
              </IconButton>
            ) : undefined
          }
        />
      </CardActionArea>

      {filter.type === 'aircraftOperator' && (
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <Divider />

          <CardContent>
            <TextField
              sx={{ mb: 2 }}
              variant="outlined"
              fullWidth
              size="small"
              label="Display name"
              value={filter.displayName}
              onChange={(ev) => {
                const newName = ev.target.value.slice(0, 30);
                actions.updateAt(idx, { ...filter, displayName: newName });
              }}
            />
            <Autocomplete
              id="aircraft-operator-filter"
              multiple
              disableClearable
              fullWidth
              autoHighlight
              value={filter.aircraftOperators}
              options={rankedAos as Array<typeof rankedAos[number] | string>}
              getOptionLabel={(item) => {
                if (typeof item === 'string') {
                  return item;
                }

                return item.commonName
                  ? `${item.commonName} (${item.prefix}, ${item.count})`
                  : `${item.prefix} (${item.count})`;
              }}
              isOptionEqualToValue={(option, value) =>
                (typeof value === 'string' ? value : value.prefix) ===
                (typeof option === 'string' ? option : option.prefix)
              }
              onChange={(ev, value) => {
                actions.updateAt(idx, {
                  ...filter,
                  aircraftOperators: value.map((v) =>
                    typeof v === 'string' ? v : v.prefix,
                  ),
                });
              }}
              filterSelectedOptions
              renderInput={(params) => (
                <TextField
                  {...params}
                  fullWidth
                  size="small"
                  variant="outlined"
                  label="Aircraft operator prefix"
                />
              )}
            />
          </CardContent>
        </Collapse>
      )}
      <Divider />
      <CardActions sx={{ justifyContent: 'flex-end' }}>
        <Box
          sx={{
            flexGrow: 1,
            flexShrink: 1,
            minWidth: 100,
            mr: 1,
          }}
        >
          {filter.type !== 'altitudeGradient' && (
            <CirclePicker
              color={{
                r: filter.color[0],
                g: filter.color[1],
                b: filter.color[2],
              }}
              circleSpacing={isMobile ? 5 : 12}
              circleSize={isMobile ? 12 : 24}
              onChangeComplete={(color) => {
                actions.updateAt(idx, {
                  ...filter,
                  color: [
                    color.rgb.r,
                    color.rgb.g,
                    color.rgb.b,
                    filter.color[3],
                  ],
                });
              }}
              width="100%"
              colors={schemeTableau10 as Array<string>}
            />
          )}
        </Box>
        {toggable && (
          <IconButton
            size="small"
            onClick={() => {
              actions.updateAt(idx, { ...filter, enabled: !filter.enabled });
            }}
          >
            {filter.enabled ? <VisibilityOffIcon /> : <VisibilityIcon />}
          </IconButton>
        )}
        {deletable && (
          <IconButton
            size="small"
            onClick={() => {
              actions.removeAt(idx);
            }}
          >
            <DeleteIcon />
          </IconButton>
        )}
      </CardActions>
    </Card>
  );
});
