import {
  Box,
  Chip,
  ChipProps,
  CircularProgress,
  createTheme,
  IconButton,
  ThemeProvider,
  Fade,
} from '@mui/material';

import React, { useMemo } from 'react';

import {
  ColorFilterState,
  useColorFilters,
  ColorFilterIcon,
  useColorBuffers,
} from '../colors';
import { Edit } from '@mui/icons-material';
import { Link as RouterLink } from 'react-router-dom';
import { getFilterGradient } from '../colors/utils';

export const MapKey: React.FC<{ disabled: boolean }> = function MapKey({
  disabled,
}) {
  const [{ filters }, { toggleFilter }] = useColorFilters();
  const { loading } = useColorBuffers();

  return (
    <Box sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
      <Box sx={{ flexShrink: 0 }}>
        <IconButton
          disabled={disabled}
          component={RouterLink}
          to="colors/edit"
          size="small"
        >
          <Edit />
        </IconButton>
      </Box>
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          flexShrink: 0,
          m: 0.5,
        }}
      >
        <Fade in={loading}>
          <CircularProgress color="inherit" disableShrink size={12} />
        </Fade>
      </Box>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'row',
          ml: 1,
          mr: 1,
          mb: -1,
          flexWrap: 'wrap',
        }}
      >
        {filters.map((f, idx) => {
          return (
            <FilterChip
              filter={f}
              sx={{
                mr: 1,
                mb: 1,
              }}
              size="small"
              disabled={disabled}
              variant={f.enabled ? 'filled' : 'outlined'}
              onClick={
                filters.length > 1
                  ? () => {
                      toggleFilter(idx);
                    }
                  : undefined
              }
              key={f.id}
            />
          );
        })}
      </Box>
    </Box>
  );
};

export const FilterChip = React.forwardRef<
  React.ComponentRef<typeof Chip>,
  ChipProps & { filter: ColorFilterState }
>(function FilterChip({ filter, sx, ...rest }, ref) {
  const theme = useMemo(() => {
    let color = filter.color;
    if (filter.type === 'altitudeGradient') {
      color = filter.bottomColor;
    }

    const filterColorString = `rgb(${color[0]}, ${color[1]}, ${color[2]})`;

    return createTheme({
      palette: {
        primary: {
          main: filterColorString,
        },
        mode: 'dark',
      },
    });
  }, [filter]);

  const gradient = useMemo(() => {
    if (!filter.enabled) {
      return;
    }
    return getFilterGradient(filter);
  }, [filter]);

  const label = rest.label ?? filter.displayName;

  return (
    <ThemeProvider theme={theme}>
      <Chip
        ref={ref}
        {...rest}
        sx={{
          background: gradient,
          ...sx,
        }}
        label={label}
        icon={rest.icon ?? <ColorFilterIcon type={filter.type} />}
        color={!gradient ? 'primary' : undefined}
      />
    </ThemeProvider>
  );
});
