import {
  Cancel as ClearIcon,
  CollectionsBookmark as PresetIcon,
  Edit as EditIcon,
  InvertColors as InvertIcon,
  Visibility as VisibilityIcon,
} from '@mui/icons-material';
import {
  Divider,
  IconButton,
  ListItemIcon,
  Menu,
  MenuItem,
  Tooltip,
} from '@mui/material';
import {
  bindMenu,
  bindTrigger,
  usePopupState,
} from 'material-ui-popup-state/hooks';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useRecoilState } from 'recoil';
import { useColorFilters } from '../colors';
import { displaySettingsAtom } from '../Map';
import { ColorPaletteIcon, NeonModeIcon, NeonModeOffIcon } from './icons';

export const PlayerControlsLeft: React.FC<{ disabled?: boolean }> =
  function PlayerControlsLeft({ disabled = false }) {
    const navigate = useNavigate();

    /**
     * This piece of state controls the display settings for the flight
     * paths.
     */
    const [displaySettings, setDisplaySettings] =
      useRecoilState(displaySettingsAtom);

    /**
     * This piece of state controls every coloring rule
     */
    const [colorFilters, { invertFilters, resetFilterStates, clearFilters }] =
      useColorFilters();

    /**
     * Filter menu state
     */
    const coloringMenuPopupState = usePopupState({
      variant: 'popover',
      popupId: 'colorMenu',
    });

    return (
      <>
        <Tooltip placement="top" title="Manage colors">
          <IconButton
            edge="start"
            disabled={disabled}
            size="small"
            {...bindTrigger(coloringMenuPopupState)}
          >
            <ColorPaletteIcon />
          </IconButton>
        </Tooltip>

        <Tooltip placement="top" title="Neon mode">
          <IconButton
            id="neon-mode"
            disabled={disabled}
            size="small"
            onClick={() => {
              setDisplaySettings((prev) => ({
                ...prev,
                blendMode: prev.blendMode === 'normal' ? 'neon' : 'normal',
              }));
            }}
          >
            {displaySettings.blendMode === 'normal' ? (
              <NeonModeOffIcon />
            ) : (
              <NeonModeIcon />
            )}
          </IconButton>
        </Tooltip>

        {/* Color menu */}
        <Menu {...bindMenu(coloringMenuPopupState)}>
          <MenuItem
            onClick={() => {
              coloringMenuPopupState.close();
              navigate('colors/presets');
            }}
          >
            <ListItemIcon>
              <PresetIcon fontSize="small" />
            </ListItemIcon>
            Presets
          </MenuItem>
          <MenuItem
            onClick={() => {
              coloringMenuPopupState.close();
              navigate('colors/edit');
            }}
          >
            <ListItemIcon>
              <EditIcon fontSize="small" />
            </ListItemIcon>
            Edit
          </MenuItem>
          <Divider />
          <MenuItem
            disabled={colorFilters.filters.length === 0}
            onClick={() => {
              coloringMenuPopupState.close();
              invertFilters();
            }}
          >
            <ListItemIcon>
              <InvertIcon fontSize="small" />
            </ListItemIcon>
            Invert
          </MenuItem>
          <MenuItem
            disabled={colorFilters.filters.length === 0}
            onClick={() => {
              coloringMenuPopupState.close();
              resetFilterStates();
            }}
          >
            <ListItemIcon>
              <VisibilityIcon />
            </ListItemIcon>
            Reset visibility
          </MenuItem>
          <Divider />
          <MenuItem
            disabled={colorFilters.filters.length === 0}
            onClick={() => {
              coloringMenuPopupState.close();
              clearFilters();
            }}
          >
            <ListItemIcon>
              <ClearIcon fontSize="small" />
            </ListItemIcon>
            Clear
          </MenuItem>
        </Menu>
      </>
    );
  };
