import React, { Suspense } from 'react';

const BaseMap = React.lazy(() => import('./BaseMap'));

export const MapLazy: React.FC<{}> = function MapLazy() {
  return (
    <div>
      <Suspense fallback={<div>Loading ...</div>}>
        <BaseMap />
      </Suspense>
    </div>
  );
};

export default MapLazy;
